
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import Vue from 'vue'
Vue.use(AlertPlugin);

export default {
  components: {
    XInput,
    XButton,
    Box,
    Divider
  },
  data () {
    return {
      mobile: "",
      validateCode: "",
      sendValidateCodeLabel:"",
      validateCodeSent:false
    }
  },
  methods: {
    sendValidateCode(){
      if(this.validateCodeSent){
        return;
      }
      if(this.mobile == ""){
        return;
      }
      this.$http.post("/sms/sendVerificationCodeSms",this.mobile);
      this.sendValidateCodeLabel = "";
      this.validateCodeSent = true;
    },
    checkValidateCode(){
      this.$http.post("/sms/validateCheckCode",this.validateCode)
        .then(response=>{return response.body})
        .then(this.handleCheckValidateCodeResponse);
    },
    handleCheckValidateCodeResponse(responseBody){
      if(responseBody && responseBody.success){
        this.$vux.alert.show({
          title: '',
          content: '',
          onHide:this.backToGoodsDetailView
        });

      }else{
        this.$vux.alert.show({
          title: '',
          content: ''
        });
      }
    },
    backToGoodsDetailView(){
      this.$router.push({path:"/goodsDetail/"+this.$route.params.goodsId});
    }
  }
}



