
import FormPreview from 'vux/src/components/form-preview/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import Vue from 'vue'
Vue.use(AlertPlugin);

export default{
  components: {
    FormPreview
  },
  data(){
    return {
      userList: []
    }
  },
  created: function () {
    var userList = this.userList;
    var vux = this.$vux;

    this.$http.get('/users').then(
      function (response) {
        var response = response.body;
        if ("0" == response.resultCode) {
          response.items.forEach(function (user) {
            var roleDesc;
            if ("normal" == user.role) {
              roleDesc = "";
            }
            if ("member" == user.role) {
              roleDesc = "";
            }
            if ("administrator" == user.role) {
              roleDesc = "";
            }
            if ("black_list" == user.role) {
              roleDesc = "";
            }
            user.fields = [
              {
                label: '',
                value: roleDesc
              },
              {
                label: '',
                value: user.nickname
              }
            ];

            if(!user.phoneNumber){
              user.phoneNumber = "";
            }
            userList.push(user);
          })
        } else {
          vux.alert.show({
            title: '',
            content: '',
            onHide () {
              window.history.back();
            }
          });
        }
      },
      function () {
        vux.alert.show({
          title: '',
          content: '',
          onHide () {
            window.history.back();
          }
        });
      }
    )
  },
  methods: {
    getButton(user){
      var httpClient = this.$http;
      var vux = this.$vux;

      var leftButton = {
        style: "primary"
      };
      var leftNextUser = JSON.parse(JSON.stringify(user));
      var rightButton = {
        style: "primary"
      };
      var rightNextUser = JSON.parse(JSON.stringify(user));


      if ("normal" == user.role) {
        leftButton.text = "";
        leftNextUser.role = "black_list";
        rightButton.text = "";
        rightNextUser.role = "member";
      }
      if ("member" == user.role) {
        leftButton.text = "";
        leftNextUser.role = "black_list";
        rightButton.text = "";
        rightNextUser.role = "normal";
      }
      if ("black_list" == user.role) {
        leftButton.text = "";
        leftNextUser.role = "normal";
        rightButton.text = "";
        rightNextUser.role = "member";
      }
      if ("administrator" == user.role) {
        leftButton.text = "";
        leftNextUser.role = "administrator";
        rightButton.text = "";
        rightNextUser.role = "administrator";
      }

      leftButton.onButtonClick = function () {
        httpClient.put('/users', leftNextUser).then(
          function (response) {
            if ("0" == response.body.resultCode) {
              vux.alert.show({
                title: '',
                content: '',
                onHide () {
                  window.location.reload();
                }
              });
            } else {
              vux.alert.show({
                title: '',
                content: ''
              });
            }
          },
          function () {
            vux.alert.show({
              title: '',
              content: ''
            });
          }
        )
      };

      rightButton.onButtonClick = function () {
        httpClient.put('/users', rightNextUser).then(
          function (response) {
            if ("0" == response.body.resultCode) {
              vux.alert.show({
                title: '',
                content: '',
                onHide () {
                  window.location.reload();
                }
              });
            } else {
              vux.alert.show({
                title: '',
                content: ''
              });
            }
          },
          function () {
            vux.alert.show({
              title: '',
              content: ''
            });
          }
        )
      };
      return [leftButton,rightButton]
    },
    tipFail(){
      this.$vux.alert.show({
        title: '',
        content: ''
      });
    }
  }
}



